:root {
  --max-w: 900px;
  --gap: 1rem;
  --accent: #0a66c2;
  --muted: #666;
  --bg: #fff;
}

body {
  color: #111;
  background: #f6f7f9;
  justify-content: center;
  margin: 0;
  padding: 2rem;
  font-family: system-ui, -apple-system, Segoe UI, Roboto, Helvetica Neue, Arial;
  display: flex;
}

.contact-page {
  width: 100%;
  max-width: var(--max-w);
  background: var(--bg);
  border-radius: 8px;
  padding: 1.5rem;
  box-shadow: 0 6px 18px #14141e0f;
}

h1 {
  margin-top: 0;
}

form {
  gap: .75rem;
  display: grid;
}

.form-group {
  gap: .35rem;
  display: grid;
}

label {
  color: var(--muted);
  font-size: .9rem;
}

input[type="text"], input[type="email"], input[type="tel"], textarea {
  font: inherit;
  background: #fff;
  border: 1px solid #ddd;
  border-radius: 6px;
  padding: .6rem;
}

textarea {
  resize: vertical;
  min-height: 120px;
}

.checkbox {
  align-items: center;
  gap: .5rem;
  display: flex;
}

.checkbox input {
  width: auto;
}

button[type="submit"] {
  background: var(--accent);
  color: #fff;
  cursor: pointer;
  border: none;
  border-radius: 6px;
  margin-top: .5rem;
  padding: .7rem 1rem;
}

.form-message {
  color: green;
  margin-top: .5rem;
}

.contact-info {
  border-left: 1px dashed #eee;
  padding: .5rem;
}

.contact-info h2 {
  margin-top: 0;
  font-size: 1.05rem;
}

.social-links {
  gap: .5rem;
  margin-top: .5rem;
  display: flex;
}

.social-links a {
  width: 36px;
  height: 36px;
  color: var(--accent);
  background: #f2f6fb;
  border-radius: 6px;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  display: inline-flex;
}

.social-links svg {
  width: 18px;
  height: 18px;
  display: block;
}

@media (width <= 720px) {
  .layout {
    grid-template-columns: 1fr;
  }

  .contact-info {
    border-top: 1px dashed #eee;
    border-left: none;
    margin-top: 1rem;
    padding-top: 1rem;
  }
}
